        // console.log('on hidden')
        $.fn.fullpage.setAllowScrolling(true)
        $('.container-more__caption').removeClass('container-more__caption--invisible')
        this.scrollTop = 0
      },
      onHide () {
        vm.currentSection.showIssueContent = false
      },
      context: '#app'
    })
  }
}
